body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.ck-content {
  min-height: 250px;
}

.blog-component {
  min-height: 350px;
}

.post-btn {
  width: 200px;
}

.nav-slider {
  cursor: pointer;
  height: 64px;
}

.blog-summary {
  background-color: #eeeeee;
}

#toast-container {
  top: 50px;
  width: 70%;
  left: 15%;
  right: 15%;
  bottom: auto;
}

.blog-title {
  font-size: 40px;
  font-weight: bold;
}

.blog-content p {
  padding-top: 10px;
  font-size: 20px;
  padding-bottom: 10px;
}